import React from 'react';

import JsonTreeCreator from './JsonTreeCreator';

function jsonTreeItems (props) { // obj{}, JTC()
    return Object.keys(props.obj).map((key) => {
        const type = typeof(props.obj[key]);
        if (type === 'string' || type === 'number' || type === 'boolean') {
            return <div key={key} className="JsonTree__Item">
                <span className={"JsonTree__Key-" + type}>{key}: </span>
                <span className={"JsonTree__Value-" + type}>
                    {type === 'string' ? JSON.stringify(props.obj[key]) : props.obj[key]}
                </span>
            </div>
        } else if (type === 'object') {
            return <JsonTreeCreator key={key} obj={props.obj[key]} objName={key} />; // object, objectName
        }
    });
}

export default jsonTreeItems;


