import React from 'react';

import './JsonTree.css';
import JsonTreeCreator from './JsonTreeCreator';
import ObjectFilter from './ObjectFilter';


class JsonTree extends React.Component {
    constructor (props) {
        super(props);

        this.state = {
            JsonObject : [],
            shouldExpand: true,
            filterText : '',
            isCaseSensitive: false
        }
    }


    componentDidUpdate (prevProps, prevState) {
      if(prevProps.obj !== this.props.obj) {
        this.setState({
          JsonObject: this.props.obj
        })
      }

      console.log('upadtes', this.props.obj)
      if (this.state.shouldExpand) {
        if (prevState.isCaseSensitive !== this.isCaseSensitive) {
          if (this.state.filterText !== '') {
            this.expandAllNodesHandler();
          }
        }
        if (prevState.filterText !== this.state.filterText) {
            this.expandAllNodesHandler();
          }
      }
    }



    getFilterTextHandler = (e) => {
      this.filterTreeHandler(this.props.obj, e.target.value, this.state.isCaseSensitive);

      this.setState({filterText: e.target.value});
    }

    filterTreeHandler = (obj, filterText, isCaseSensitive) => {
      let updatedObj = ObjectFilter(obj, filterText, isCaseSensitive);
      this.setState({JsonObject: updatedObj});
    }

    caseSensitiveHandler = (e) => {
      this.filterTreeHandler(this.props.obj, this.state.filterText, e.currentTarget.checked);
      
      this.setState({isCaseSensitive: e.currentTarget.checked});
    }
    



    // Menu Buttons...........
    collapseAllNodesHandler = () => {
      let elementRef = document.querySelectorAll('.JsonTree__Object-Data');
      for (let i=0; i < elementRef.length; i++) {
        elementRef[i].classList.remove('show');
        elementRef[i].previousSibling.firstChild.firstChild.innerHTML = '&#9658'
      }
    }

    expandAllNodesHandler = () => {
      let elementRef = document.querySelectorAll('.JsonTree__Object-Data');
      for (let i=0; i < elementRef.length; i++) {
        elementRef[i].classList.add('show');
        elementRef[i].previousSibling.firstChild.firstChild.innerHTML = '&#9660'
      }
    }

    render () {
        const {errorName, errorMessage} = this.props.jsonError;

        return <div className="JsonTree">
          <div className="JsonTree__Header">
            <div className="JsonTree__Filter-Search">
              <input 
                type="text" 
                onChange={this.getFilterTextHandler} 
                placeholder="Filter Object" />
              <label className="JsonTree__Filter-Type">
                <input type="checkbox" onChange={this.caseSensitiveHandler} checked={this.state.isCaseSensitive} />
                Case Sensitive Search
              </label>
            </div>

            <div className="JsonTree__Customization-Buttons">
              <button onClick={this.collapseAllNodesHandler} className="JsonTree__Collapse">Collapse all DOM Nodes</button>
              <button onClick={this.expandAllNodesHandler} className="JsonTree__Expand">Expnad all DOM Nodes</button>
            </div>
          </div>

          <div className="JsonTree__Container">
              {
                errorName !== '' ?
                  <div>
                    <div> Error Type: {errorName} </div>
                    <div> Error Message: {errorMessage} </div>
                  </div> :
                  <JsonTreeCreator obj={this.state.JsonObject} objName='JSON Tree' />
              }
              
          </div>
        </div>
        
    }
}

export default JsonTree;


