
const objectFilter = (obj, text, caseSensitive) => {
    let filteredObject = {};
    if (obj.length) {
      filteredObject = [];  // for not converting array into object
    }

    if (text === '') { // returns obj immediately for empty search
        return obj;
    }

    Object.keys(obj).map((key) => {
      const type = typeof(obj[key]);
      if (type === 'string' || type === 'number' || type === 'boolean') {
        // for Case Sensitive Seacrh...
        // Change key, value, and search Text to lowercase for search indexof()..
        let CSkey = key;
        let CSvalue = JSON.stringify(obj[key]);
        let CStext = text;
        if (!caseSensitive) {
            CSkey = key.toLowerCase();
            CSvalue = JSON.stringify(obj[key]).toLowerCase();
            CStext = text.toLowerCase();
        }

        if (CSkey.indexOf(CStext) !== -1 || CSvalue.indexOf(CStext) !== -1) {
          if (obj.length) { // 
            filteredObject.push(obj[key]);
          } else {
            filteredObject[key] = obj[key];
          }
        }
      } else if (type === 'object') {
        let tempObj = objectFilter(obj[key], text, caseSensitive);
        if (Object.keys(tempObj).length) { // don't add key if object length is '0'..
          filteredObject[key] = tempObj;
        }
      }
      return null;
    });

    return filteredObject;
}


export default objectFilter;


