import React from 'react';

import JsonTreeItems from './JsonTreeItems';

const dropdownTogglerHandler = (e) => {
    if (e.currentTarget.parentNode.nextSibling.classList.toggle('show')) { // this toggles show and returns true on add
        e.currentTarget.firstChild.innerHTML = '&#9660;'
    } else {
        e.currentTarget.firstChild.innerHTML = '&#9658;';
    }
    // e.currentTarget.parentNode.nextSibling.classList.toggle('show');
}

// 9654, 9660 right arrkey and downarrkey

const jsonTreeCreator = (props) => { // obj, objName
    return <div className="JsonTree__Object-Box">
        <div className="JsonTree__Object-Details">
            <span className="JsonTree__Object-Name" onClick={dropdownTogglerHandler}>
                <span className="JsonTree__Symbol">&#9658;</span> 
                {props.objName}: </span>

            <span className="JsonTree__Object-Type">
                { props.obj.length ? 'Array' : 'Object' }
                ({ Object.keys(props.obj).length })
            </span>
        </div>

        <div className="JsonTree__Object-Data">
            <JsonTreeItems obj={props.obj} />
        </div>
    </div>
}

export default jsonTreeCreator;


