import React, { Component } from 'react';
import './App.css';

import JsonTree from './JsonTree/JsonTree';



class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      jsonData: [],
      jsonError: {
        errorName: '',
        errorMessage: ''
      } 
    }
    this.inputRef = React.createRef();
  }

  componentDidUpdate() {
    console.log(this.state)
  }

  handleJsonObj() {
    const jsonInput = this.inputRef.current.value;
    try {
      if(jsonInput === '') throw {name: 'empty', message: 'No value Provided'};

      let parsedInput = JSON.parse(jsonInput);
      if (typeof parsedInput === 'string')  throw {name: 'data type String', message: 'Value you provided is a string'};

      if(parsedInput) {
        this.setState({
          jsonData: parsedInput,
          jsonError: {
            errorName: '',
            errorMessage: ''
          } 
        })
      }
    } catch(err) {
      this.setState({
        jsonError: {
          errorName: err.name,
          errorMessage: err.message
        },
        jsonData: []
      })
    }
  }

  render() {
    return (
      <div className="App">
        <div className="JsonInput_box">
          <input 
            type='text' 
            ref={this.inputRef} 
            className="jsonInput"
            placeholder='Paste JSON here...' />
          <button 
            className="createTreeButton" 
            onClick={() => this.handleJsonObj()}>Create Tree</button>
        </div>

        <JsonTree obj={this.state.jsonData} jsonError={this.state.jsonError} />
      </div>
    );
  }
}

export default App;



